<template>
  <div class="yeardetial">
    <sHeader :name= "title"  />
    <van-collapse v-for="(item, index) in yearList" :key="index" v-model="item.active" accordion>
      <van-collapse-item  :title="item['date']" :name="1" >
        <div class="list_record" v-for="(childItem, childIndex) in item['childList']" :key="childIndex" >
          <div class="item_style">{{ childItem['drinkTimeDesc'] }}</div>
          <div class="item_style" >第{{ childIndex + 1 }}次</div>
          <div class="item_style">{{ childItem['drinkAmount'] }}ml</div>
        </div>
      </van-collapse-item>
    </van-collapse>
  </div>
</template>

<script>
import {onMounted, reactive, toRefs} from "vue";
import sHeader from '@/components/SimpleHeader'
import { useRoute } from "vue-router";

export default {
  name: "YearDetialList",
  components:{
    sHeader
  },
  setup(){
    const router = useRoute()

    /**
     * 声明
     * @type {UnwrapNestedRefs<{list: []}>} 数据列表
     */
    const state = reactive({
      yearList:[],
      params: {},
      title:""
    })

    onMounted(()=>{
      const { query:{ date = "", itemJson } } = router
      console.log('query',JSON.parse(itemJson))
      // state.params = query.item
      state.title = date
      state.yearList = JSON.parse(itemJson)
    })

    return {
      ...toRefs(state)
    }
  }
}
</script>

